import common from '../../common/common.js'
import util from '../../common/util.js'

export default {
	data() {
		return {
			baseurl: common.baseUrl,
			ads: [],
			mobile: '',
			userInfo: [],
			tfrom:'',
			userid:0,
			id:0,
		}
	},
	onLoad(options) {
		uni.setNavigationBarTitle({
		  title: '用户登录'
		})
		var that = this
		var userid = uni.getStorageSync("userid");
		var mobile = uni.getStorageSync("mobile");
		var userInfo = uni.getStorageSync("userInfo");

		var tfrom = options.from ? options.from : '';
		var id = parseInt(options.id)
		that.userid = userid
		that.mobile = mobile
		that.userInfo = userInfo
		that.tfrom = tfrom
		that.id = id

		that.getLoginBgAds();
	},
	onPullDownRefresh(){
	  var that = this
	  uni.showNavigationBarLoading();
	  console.log('下拉刷新中...');	
	  that.getLoginBgAds();
	  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	methods: {
		getPhoneNumber: function (e) {
			var that = this
			
			if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
			  uni.showModal({
				title: '提示',
				showCancel: false,
				content: '未授权',
				success: function (res) { }
			  })
			} else {
			  uni.login({
				success: function (res) {
				  var code = res.code
				  var url = common.apiUrl +'user/userAuthMobile';
				  var uid = uni.getStorageSync("uid");
				  var tjuid = uni.getStorageSync("tjuid");
				  console.log("code",code)
				  uni.request({
					url: url,
					data: {
					  uid: uid,
					  tjuid: tjuid,
					  code: code,
					  iv: e.detail.iv,
					  encryptedData: e.detail.encryptedData,
					},
					method: "GET",
					// header: {
					//   "Content-Type": "application/x-www-form-urlencoded"
					// },
					success: function (res) {
					  console.log(res);
					  if (res.data.code == 0) {
						that.mobile = res.data.userInfo.users_mobile;
						console.log(that.mobile)
						uni.setStorageSync("userInfo", res.data.userInfo);
						uni.setStorageSync("userid", res.data.userInfo.users_id);
					  } else {
						
						uni.showToast({ title: '处理失败', icon: 'none', duration: 1000 })
					  }

					}
				  })
				}
			  });

			}
		  },


		  userInfoHandler: function (e) {
			var that = this
			uni.getSetting({
			  success(res) {
				//console.log(res);
				if (!res.authSetting['scope.userInfo']) {
				  console.log('------没有授权----')
				} else {
				  //console.log('checkUserLogin', e.detail)
				  that.checkUserLogin(e.detail);
				}
			  }
			})
		  },

		  checkUserLogin: function (e) {
			var that = this;
			var userInfo = uni.getStorageSync("userInfo");
			//if (uid == '') {
			  uni.login({
				success: function (res) {
				  //console.log(res)
				  var code = res.code;
				  var rawData = e.rawData
				  var encryptedData = e.encryptedData
				  var iv = e.iv
				  var url = common.apiUrl + 'user/updateUserInfo';
				  var tjuid = uni.getStorageSync("tjuid");
				  var tpuid = uni.getStorageSync("tpuid");
				  uni.request({
					//用户登陆URL地址，请根据自已项目修改
					url: url,
					method: "GET",
					data: {
					  uid: userInfo.users_id,
					  tjuid: tjuid,
					  tpuid: tpuid,
					  code: code,
					  rawData: rawData,
					  encryptedData: encryptedData,
					  iv: iv
					},
					fail: function (res) {

					},
					success: function (res) {
					  var userInfo = res.data.userInfo;
					  //设置用户缓存
					  uni.setStorageSync("userInfo", userInfo);
					  console.log(userInfo)
					  console.log('登录成功');
					  uni.showToast({
						title: '操作成功',
						icon: 'success',
						duration: 1000,
						success() {
						  setTimeout(function () {
							//var userInfo = uni.getStorageSync("userInfo");
							that.userInfo = userInfo
							//if (!!userInfo.mem_mobile) {
							  var tfrom = that.tfrom
							  var id = that.id
							  if (!tfrom) {
								uni.reLaunch({
								  url: '/pages/user/user',
								})                      
							  } else if (tfrom == 'goods' && id > 0) {
								uni.navigateTo({
								  url: '/pages/goods/goods?id=' + id,
								})
							  } else {
								uni.reLaunch({
								  url: '/pages/user/user',
								})
							  }
							//}
							console.log('userInfo', that.userInfo)

						  }, 1500) //延迟时间
						}
					  })
					}
				  })
				},
				fail: function (res) {
				  console.log(res);
				}
			  })
			//}
		  },

		  //获取背景广告
		  getLoginBgAds: function () {
			var that = this
			uni.request({
			  url: common.apiUrl +'ads/getLoginBgAds',
			  method: "GET",
			  data: {
			  },
			  success(res) {
				that.ads = res.data.ads,
				console.log(res)
			  }
			})
		  },
	}
}
